import Vue from 'vue'
import Vuex from 'vuex'
import user from './user'
import { resolve } from 'path';

Vue.use(Vuex)

export default new Vuex.Store({
	modules: {
		user
	},
	state: {
		installPrompt: null,
		allowInstall: true,
	},
	mutations: {
		setInstallPrompt(state, payload) {
			state.installPrompt = payload;
			state.installPrompt.userChoice
				.then(resolve => {
					if (resolve.outcome !== "dismissed") {
						state.allowInstall = false;
					}
				})
		}
	},
	actions: {
		setInstallPrompt({ commit }, payload) {
			commit('setInstallPrompt', payload);
		}
	},
	getters: {
		canInstall(state) {
			return 'BeforeInstallPromptEvent' in window &&
				state.installPrompt instanceof BeforeInstallPromptEvent &&
				state.allowInstall
		},
		installPrompt(state) {
			return state.installPrompt;
		}
	}
})
